.header {
  position: relative;
}

.header__wrapper {
  margin: 0;
  display: flex;
  flex-direction: column;
}

.header__container {
  display: flex;
  justify-content: space-between;
  padding-top: 11px;
  padding-bottom: 13px;
  border-bottom: 1px solid #e6e6e6;
}

.header__logo {
  display: block;
  width: 100%;
  max-width: 190px;
}

  @media (min-width: $mobile-width) and (max-width: $max-mobile-width) {
    .header__container {
      position: relative;
    }

    .header__logo {
      margin-left: 20px;
    }

    .header__container::after {
      display: none;
      position: absolute;
      content: ("");
      top: 23px;
      right: 23px;
      background-image: url("../img/icon-close.svg");
      width: 18px;
      height: 18px;
    }
  }

@media (min-width: $tablet-width) {
  .header__container {
    border-bottom: none;
  }

  .header__logo:hover,
  .header__logo:focus {
    opacity: 0.4;
  }

  .header__logo:active {
    opacity: 0.2;
  }

  @media (min-width: $tablet-width) and (max-width: $max-tablet-width) {
  .header__wrapper {
    width: 100%;
    margin: 0;
    margin-top: 25px;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .header__container {
    padding: 0;
    padding-left: 30px;
  }

  .header__logo {
    max-width: 174px;
    width: 174px;
    height: auto;

  }
}
}

@media (min-width: $desktop-width) {
  .header {
    color: #ffffff;
    padding-top: 50px;
  }

  .header__container{
    padding: 0;
  }

  .header__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-left: 40px;
    padding-right: 40px;
  }

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    .header--background::after {
      background-image: url("../img/index-background-desktop@2x.jpg");
    }
  }

  .header--background::after {
    display: block;
    position: absolute;
    content: "";
    width: 50%;
    min-height: 696px;
    top: 0;
    right: 0;
    left: inherit;
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-color: $bg-green;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -2;
  }

  .header__logo {
    max-width: 202px;
    width: 202px;
    height: auto;

  }
}
